.task-container{
    background-color: #444;
    margin: 8px 0;
    padding: 15px 20px;
    display: flex;
    border-radius: 5px;
    justify-content: space-between;
    color: #eee;
    align-items: center;
}

.task-title{
    cursor: pointer;
}

.buttons-container{
    width: 30%;
    display: flex;
    justify-content: flex-end;
}

.remove-task-button, .see-details-task-button{
    background-color: #444;
    border: none;
    font-size: 22px;
    color: var(--cor-roxa); 
    margin: 0 5px;
    cursor: pointer;
}